* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  padding: 15px 60px;
}

.bd {
  border: 1px solid #cc0000;
}

// font setting

@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP:400,700);

@mixin link() {
  background: linear-gradient(transparent 60%, #ff6 60%);
  box-shadow: none;
}

html {
  font-size: 15px;

  &[lang="ja"] {
    body,
    button,
    input,
    select,
    textarea {
      font-family: 'Noto Sans JP', 'Hiragino Kaku Gothic Pro', Meiryo, sans-serif;
    }
  }
}

// front-page

.mainVisual > .jumbotron {
  background: url("../img/IMG_104_kako.jpg");
  color: #ffffff;
  padding: 300px 0;
  margin-bottom: 0;
  background-position: center center;
  background-size: cover;
  h1{
    text-shadow: 1px 1px 1px #444;
    font-size: 2rem;
  }
  h1 img{
    display: block;
    margin:0 auto;
    padding-bottom:30px;
  }
}
@media screen and (min-width: 768px) {
   .mainVisual > .jumbotron {
     h1{
      font-size: 3rem;
    }
  }
}

.catchCopy {
  text-align: center;
  padding: 200px 0;
  background: url(../img/IMG_012_kako.jpg);
  background-position: center center;
  background-size: cover;
  & > p {
    max-width: 200px;
  }

}

.service,
.developmentSupport,
.aboutVisual,
.okinawaMember,
.tokyoMember,
.sns,
.commutySupport,
.partner,
.whatsNew {
  text-align: center;
  padding: 50px 0;
  h2 {
    padding: 50px 0 50px 0;
  }
  h3 {
    padding: 50px 0 40px 0;
  }
