}

.service {
  button {
    border-radius: 0px;
    margin-bottom: 20px;
  }
  .books {
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
    width: 133px;
  }
}

.btn-ruby {
  background-color: $ruby;
  color: #ffffff;
  font-weight: normal;
}

.developmentSupport {
  background-color: #EAEAEA;
}

.aboutVisual {
  h2 {
    padding: 50px 0 50px 0;
  }
  img {
    margin-bottom: 20px;
  }
}

.cardMember {
  border: none;
  padding: 50px;
}

.okinawaMember,
.tokyoMember {
  padding: 0;
  h3 {
    padding: 20px;
  }
}

.sns {
  .card {
    border-radius: 12px;
    border: 2px #EAEAEA solid;
    dl {
      padding: 20px 0;
      dt {
      }
      dr {
        font-size: 2rem;
        text-align: left;
      }
    }
    &__icon {
      padding: 20px 0;
      border-bottom: 2px #EAEAEA solid;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
      img {
        height: 70px;
        margin-bottom: 0;
      }
    }
    &__qiita {
      .card__icon {
        background-color: #72C13A;
      }
    }
    &__github {
      .card__icon {
        background-color: #000000;
      }
    }
  }
}

.commutySupport {
  background-color: #EAEAEA;
  p {
    margin-bottom: 50px;
  }
}

.partner {
  padding: 100px 0;
  img {
    margin-bottom: 0;
  }
}

.whatsNew {
  border-top: #EAEAEA 3px solid;
}

.siteHeader {
  .navbar {
    .navbar-nav {
      .nav-item {
        padding: 0 15px;

      }
      .nav-link {
        font-size: $font-size-sm;
      }
    }
  }
}

// page

.pageVisual > .jumbotron {
  background: url("https://picsum.photos/1467/815?image=817");
  color: #ffffff;
  padding: 100px 0;
  margin-bottom: 0;
}

.content {
  ul {
    list-style: none;
  }
  .profile {
    text-align: center;
    &__sns {
      padding: 0;
      &__icon {
        display: inline-block;
        padding: 0 5px;
      }
    }
  }
}

.blue-text {
  color: $blue;
}

.company {
  table {
  }
  tr {
    height: 100px;
  }
  th, td {
    vertical-align: top;
  }
  th {
    width: 100px;
  }
  img {
    margin-right: 20px;
  }
}

footer {
  padding: 100px;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .siteHeader {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
  }
  body {
    padding-top: 49px;
  }
}